.projectStyle > .ant-row,
.ant-form-item,
.sc-AxjAm,
.StDqN {
  display: flex;
  flex-direction: column;
  text-align: left;
  width: 100%;
  margin: 0 auto 20px auto;
}

.ant-col,
.ant-form-item-label {
  text-align: left;
}

.projectStyleCol {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  width: 85%;
  margin: 0 auto;
  margin-bottom: 20px;
}

.projectStyleCol > div {
  width: 120px;
}

.projectStyleCol > div:last-child {
  width: 150px;
}

.projectStyleSecond {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  align-content: space-between;
  width: 150px;
}

.projectStyleSecond > div:last-child {
  width: 150px;
}

